              <Link 
                href="/contact" 
                className={`px-4 py-4 rounded-lg transition ${isActive('/contact') ? 'bg-white/20 font-bold' : 'hover:bg-white/10'}`}
                onClick={() => setIsMenuOpen(false)}
              >
                Contact
              </Link>
              
